.myPetsContainer {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: auto;
}

.Pets {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  padding-bottom: 10vh;
  width: 100vw;
}

.BirthdayContainer a,
.BirthdayText {
  text-decoration: none;
  color: white;
  margin: 0;
}

.selectContainer {
  font-family: "Dosis", sans-serif;
  background-color: rgb(232, 240, 254);
  width: 30vw;
  height: 60px;
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  color: rgb(74, 73, 73);
  font-weight: bolder;
}

.button {
  display: flex;
  background-color: #ff5500;
  color: #fff;
  text-decoration: none;
  padding: 10px 30px;
  border-radius: 5px;
  font-weight: bold;
  font-family: "Dosis", sans-serif;
  border: none;
  font-size: inherit;
  letter-spacing: 2px;
  cursor: pointer;
}

.PetList {
  width: 35vw;
  font-size: large;
  display: flex;
  flex-direction: column;
  margin-right: 60px;
}

.InputContainer {
  width: 35vw;
}

.dataContainer {
  display: flex;
  flex-direction: column;
}

.delete {
  background-color: #333;
  color: #fff;
  text-decoration: none;
  padding: 10px 30px;
  border-radius: 5px;
  font-weight: bold;
  font-family: "Dosis", sans-serif;
  border: none;
  font-size: inherit;
  letter-spacing: 2px;
  cursor: pointer;
  margin-top: 5px;
}

.child {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 25px;
  overflow-wrap: anywhere;
}

.profilePic {
  width: 150px;
  border-radius: 50%;
  margin: 5px;
}

.female {
  background-color: rgb(255, 110, 159, 0.5);
  border: 10px solid #333;
  padding: 10px;
  margin-bottom: 10px;
}

.male {
  background-color: rgba(122, 146, 254, 0.5);
  border: 10px solid #333;
  padding: 10px;
  margin-bottom: 10px;
}

@media (max-width: 1000px) {
  .Pets {
    flex-direction: column;
  }

  .PetList,
  .InputContainer,
  .selectContainer {
    width: 100vw;
    margin: 0;
    padding-bottom: 5vh;
  }

  .myPetsContainer h2 {
    padding-left: 5px;
  }

  form {
    margin-bottom: 2vh;
  }

  .selectContainer {
    margin-bottom: 5px;
    padding: 5px 0 5px 5px;
    width: 99vw;
  }

  .button {
    width: 100vw;
    justify-content: center;
  }
}
